import React from 'react'

export default function About() {
    return (
        <div className='about-page'>
            <h2 className='display-2'>Navy</h2>
            <p className='lead'>A navy or sea force is the branch of a nation's armed forces principally designated for naval and amphibious warfare; namely, lake-borne, riverine, littoral, or ocean-borne combat operations and related functions. It includes anything conducted by surface ships, amphibious ships, submarines, and seaborne aviation, as well as ancillary support, communications, training, and other fields. The strategic offensive role of a navy is projection of force into areas beyond a country's shores (for example, to protect sea-lanes, deter or confront piracy, ferry troops, or attack other navies, ports, or shore installations). The strategic defensive purpose of a navy is to frustrate seaborne projection-of-force by enemies. The strategic task of the navy also may incorporate nuclear deterrence by use of submarine-launched ballistic missiles. Naval operations can be broadly divided between riverine and littoral applications (brown-water navy), open-ocean applications (blue-water navy), and something in between (green-water navy), although these distinctions are more about strategic scope than tactical or operational division.

In most nations, the term "naval", as opposed to "navy", is interpreted as encompassing all maritime military forces, e.g., navy, naval infantry/marine corps, and coast guard forces.</p>
            <p className='lead'>Due to the lack of knowledge and appreciation regarding Navy and the great role naval forces play in defence and attack, we decided to create a platform in order to share our slight information and admiration of the history of naval forces around the world. We're willing to cover all the great nations with remarkable navies.</p>
            {/* eslint-disable-next-line */}
            <h4 className='display-4'>Contact us at <a>rallendalle@gmail.com</a></h4>
        </div>
    )
}
